*{
  font-family: sans-serif;
}

h1{
  text-align: center;
  font-size: 40px;
}
h2, h3, h4, h5, h6{
  font-weight: 300;
}

body {
  background-color: white;
  top: 0;;
}

article{
  border-bottom: 1px solid #eee;
  padding: 30px 0;
}

.page-content{
  margin-top: 10px;
  justify-content: center;
  margin-left: 20px;
}

nav{
    background-color: black;
    padding-top: 10px;
    padding-bottom: 10px;
    
}

Form{
  float: right;
  margin-right: 10px;
}

Link{
  font-size: 50px;
  align-content: center;
  align-items: center;
}

.navbar-menu{
  align-items: right;
  align-self: flex-end;
  padding-top: 5px;
}

.brand-text{
  margin-bottom: 10px;
  float: right;
  margin-top: 5px;
  margin-right: 10px;
  margin-left: 5px;
  text-decoration: none;
  color: #eee;
  size: 20px;
}

.brand-text:hover{
  color: grey;
}

.navbar-item{
  color: white;
  text-decoration: none;
  font-family: 'Times New Roman';
  align-self: flex-end;
  padding-right: 10px;
  padding-left: 10px;
}

FormControl{
  margin-right: 20px;
  padding: 2px;
}

Button{
  background: transparent;
  color: #eee;
  border: 1px solid white;
  border-radius: 5px;
  margin-left: 10px;
  padding: 3px;
}

.navbar{
  top: 0;
  z-index: 2;
}


